"use client";

import { useEffect, useState } from "react";
import Header from "@/src/components/landingpage/Header";
import Footer from "@/src/components/landingpage/Footer";
import UserTopMenu from "@/src/components/landingpage/UserTopMenu";
import NotificationDialog from "@/src/components/ui/NotificationDialog";
import { useUser } from "@/src/context/AuthContext";
import { useVehicleIssues } from "@/src/hooks/useVehicleIssues";
import { useTechnicianMap } from "@/src/hooks/useTechnicianMap";
import { ExtendedVehicleIssue, VehicleIssueStatus } from "@/src/lib/vehicleIssues/vehicleIssueTypes";
import VehicleIssueList from "@/src/components/vehicleIssues/VehicleIssueList";
import { collection, getDocs, query, where } from "firebase/firestore";
import { db } from "@/src/firebaseConfig";
import { Input } from "@/src/components/ui/input";
import { Textarea } from "@/src/components/ui/textarea";
import { Button } from "@/src/components/ui/button";

export default function TechnicianDashboard() {
  const { user, role, companyId } = useUser();
  const [notification, setNotification] = useState<string | null>(null);
  const [checkingRole, setCheckingRole] = useState(true);
  const [isTechnician, setIsTechnician] = useState(false);

  const { technicianMap, loading: technicianMapLoading } = useTechnicianMap(companyId ?? undefined);
  const { issues, updateIssue, loading: issuesLoading } = useVehicleIssues({
    companyId: companyId ?? undefined,
    role: role ?? undefined,
    technicianUserId: isTechnician ? user?.uid : undefined
  });

  const [proposeModal, setProposeModal] = useState<ExtendedVehicleIssue | null>(null);
  const [solution, setSolution] = useState("");
  const [cost, setCost] = useState("");

  useEffect(() => {
    if (!user) return;

    const checkRole = async () => {
      const q = query(collection(db, "staffs"), where("userId", "==", user.uid));
      const snap = await getDocs(q);

      if (!snap.empty) {
        const staff = snap.docs[0].data();
        setIsTechnician((staff.role || "").toLowerCase() === "technician");
      }

      setCheckingRole(false);
    };

    checkRole();
  }, [user]);

  if (!user || checkingRole) return <div className="text-center py-10 text-gray-500">üîé Checking access...</div>;
  if (!isTechnician) return <div className="text-center py-10 text-red-500">üö´ Access denied. Technician only.</div>;
  if (issuesLoading || technicianMapLoading) return <div className="text-center py-10">‚è≥ Loading...</div>;

  const handleUpdateStatus = async (issue: ExtendedVehicleIssue, newStatus: VehicleIssueStatus) => {
    await updateIssue(issue.id, { status: newStatus });
    setNotification(`Updated issue status to "${newStatus}"`);
  };

  const handlePropose = async () => {
    if (!proposeModal) return;

    await updateIssue(proposeModal.id, {
      solution,
      cost: parseFloat(cost),
      status: "proposed",
    } as any);

    setNotification("Proposed solution and cost successfully");
    setProposeModal(null);
    setSolution("");
    setCost("");
  };

  return (
    <div className="min-h-screen flex flex-col bg-gray-50">
      <Header />
      <UserTopMenu />
      <main className="flex-1 p-6 mx-auto space-y-6 max-w-3xl">
        <h1 className="text-2xl font-bold text-gray-800">üîß Technician Dashboard</h1>

        {issues.map((issue) => (
          <div key={issue.id} className="bg-white rounded-lg shadow p-4 space-y-2">
            <div><strong>VIN:</strong> {issue.vin}</div>
            <div><strong>Plate:</strong> {issue.plateNumber}</div>
            <div><strong>Issue:</strong> {issue.issueType}</div>
            <div><strong>Status:</strong> {issue.status}</div>

            {issue.status === "assigned" && (
              <Button onClick={() => setProposeModal(issue)}>
                Propose Solution
              </Button>
            )}

            {issue.status === "proposed" && (
              <div className="text-blue-600 text-sm">‚úÖ Proposed. Waiting for approval.</div>
            )}

            {issue.status === "in_progress" && (
              <Button variant="secondary" onClick={() => handleUpdateStatus(issue, "resolved")}>
                Mark as Resolved
              </Button>
            )}
          </div>
        ))}

        {/* Propose Modal */}
        {proposeModal && (
          <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-6 z-50">
            <div className="bg-white p-6 rounded-lg space-y-4 max-w-md w-full">
              <h2 className="text-xl font-bold">Propose Solution</h2>

              <Textarea placeholder="Describe the solution..." value={solution} onChange={(e) => setSolution(e.target.value)} />

              <Input type="number" placeholder="Estimated cost (VND)" value={cost} onChange={(e) => setCost(e.target.value)} />

              <div className="flex justify-end gap-2">
                <Button variant="ghost" onClick={() => setProposeModal(null)}>Cancel</Button>
                <Button onClick={handlePropose}>Submit Proposal</Button>
              </div>
            </div>
          </div>
        )}
      </main>

      <Footer />
      <NotificationDialog
        open={!!notification}
        type="success"
        title="Success"
        description={notification || undefined}
        onClose={() => setNotification(null)}
      />
    </div>
  );
}
