'use client';

import { useEffect, useState } from 'react';
import { db } from '@/src/firebaseConfig';
import { collection, getDocs, query, where, doc, getDoc } from 'firebase/firestore';
import { useUser } from '@/src/context/AuthContext';
import { Loader } from 'lucide-react';
import { Button } from '@/src/components/ui/button';
import { useRouter } from 'next/navigation';

import Header from '@/src/components/landingpage/Header';
import Footer from '@/src/components/landingpage/Footer';

interface StaffEntry {
  id: string;
  companyId: string;
  stationId?: string;
  role: string;
  companyName?: string;
  stationName?: string;
}

export default function StaffDashboard() {
  const { user } = useUser();
  const router = useRouter();
  const [loading, setLoading] = useState(true);
  const [staffRoles, setStaffRoles] = useState<StaffEntry[]>([]);

  useEffect(() => {
    const fetchStaffRoles = async () => {
      if (!user?.uid) return;

      try {
        const snap = await getDocs(query(collection(db, 'staffs'), where('userId', '==', user.uid)));
        const results: StaffEntry[] = [];

        for (const docSnap of snap.docs) {
          const data = docSnap.data();
          const staffEntry: StaffEntry = {
            id: docSnap.id,
            companyId: data.companyId,
            stationId: data.stationId || '',
            role: data.role,
          };

          const companySnap = await getDoc(doc(db, 'rentalCompanies', data.companyId));
          if (companySnap.exists()) {
            staffEntry.companyName = companySnap.data().name;
          }

          if (data.stationId) {
            const stationSnap = await getDoc(doc(db, 'rentalStations', data.stationId));
            if (stationSnap.exists()) {
              staffEntry.stationName = stationSnap.data().name;
            }
          }

          results.push(staffEntry);
        }

        setStaffRoles(results);
      } catch (err) {
        console.error('‚ùå Error fetching staff roles:', err);
      } finally {
        setLoading(false);
      }
    };

    fetchStaffRoles();
  }, [user]);

  if (loading) {
    return (
      <div className="flex justify-center items-center py-10">
        <Loader className="animate-spin w-6 h-6 text-gray-500" />
      </div>
    );
  }

  if (!staffRoles.length) {
    return (
      <div className="min-h-screen flex flex-col">
        <Header />
        <main className="flex-1 flex items-center justify-center text-gray-500 text-lg">No staff roles found.</main>
        <Footer />
      </div>
    );
  }

  return (
    <div className="flex flex-col min-h-screen">
      <Header />
      <main className="flex-1 p-6 space-y-6 bg-gray-50">
        <h1 className="text-3xl font-bold text-center text-gray-800">üë∑ Staff Dashboard</h1>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          {staffRoles.map((staff) => (
            <div
              key={staff.id}
              className="border p-6 rounded-xl bg-white shadow space-y-4"
            >
              {/* Info */}
              <div className="space-y-2 text-gray-700">
                <div><strong>üè¢ Company:</strong> {staff.companyName || 'N/A'}</div>
                <div><strong>üìç Station:</strong> {staff.stationName || 'No Station'}</div>
                <div><strong>üß∞ Role:</strong> <span className="capitalize">{staff.role}</span></div>
              </div>

              {/* Quick actions */}
              <div>
                <h3 className="text-sm font-semibold text-gray-700 mb-2">Quick Actions</h3>
                <div className="grid grid-cols-2 gap-3">
                  {(staff.role === 'support' || staff.role === 'station_manager') && (
                    <>
                      <Button onClick={() =>
                        router.push(`/rent?companyId=${staff.companyId}&stationId=${staff.stationId}`)
                      }>
                        üö≤ Rent a Bike
                      </Button>
                      <Button onClick={() =>
                        router.push(`/return?companyId=${staff.companyId}&stationId=${staff.stationId}`)
                      }>
                        üîÑ Return eBike
                      </Button>
                      <Button onClick={() =>
                        router.push(`/vehicle-issues/report?companyId=${staff.companyId}&companyName=${encodeURIComponent(staff.companyName || '')}&stationId=${staff.stationId}&stationName=${encodeURIComponent(staff.stationName || '')}`)
                      }>
                        üö® Report Vehicle Issue
                      </Button>
                      {staff.role === 'station_manager' && (
                        <Button onClick={() =>
                          router.push(`/stats?companyId=${staff.companyId}&stationId=${staff.stationId}`)
                        }>
                          üìä Station Stats
                        </Button>
                      )}
                    </>
                  )}

                  {staff.role === 'technician' && (
                    <Button onClick={() => router.push('/maintain')}>
                      üõ† Maintenance
                    </Button>
                  )}

                  {staff.role === 'company_admin' && (
                    <>
                      <Button onClick={() => router.push('/manage-staff')}>
                        üë• Manage Staff
                      </Button>
                      <Button onClick={() => router.push('/ebikes')}>
                        üö≤ Manage eBikes
                      </Button>
                      <Button onClick={() => router.push('/rental-orders')}>
                        üì¶ Manage Orders
                      </Button>
                    </>
                  )}
                </div>
              </div>
            </div>
          ))}
        </div>
      </main>

      <Footer />
    </div>
  );
}
